"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { useAuth } from "@/context/AuthContext";
import { roomsApi, Match } from "@/lib/api";
import { formatTimeAgo, getRatingChangeColor, getResultColor } from "@/lib/utils";
import {
    Microscope,
    RotateCcw,
    Zap,
    Trophy,
    Clock,
    Eye,
    ChevronRight,
    Crown,
    Users,
} from "lucide-react";

export default function DashboardPage() {
    const { user } = useAuth();
    const [recentMatches, setRecentMatches] = useState<Match[]>([]);
    const [lastMatch, setLastMatch] = useState<Match | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            const { data } = await roomsApi.getHistory(1, 5);
            if (data?.matches) {
                setRecentMatches(data.matches);
                setLastMatch(data.matches[0] || null);
            }
            setIsLoading(false);
        };
        fetchData();
    }, []);

    return (
        <div className="p-8">
            {/* Last Match Banner */}
            {lastMatch && (
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className={`rounded-lg overflow-hidden mb-8 ${lastMatch.result === "win"
                            ? "bg-gradient-to-r from-accent/20 to-accent/5"
                            : lastMatch.result === "loss"
                                ? "bg-gradient-to-r from-gray-900 to-background-secondary"
                                : "bg-gradient-to-r from-gray-800 to-background-secondary"
                        }`}
                >
                    <div className="flex items-center p-6">
                        {/* Chess board thumbnail */}
                        <div className="w-48 h-36 bg-background-card rounded-lg flex items-center justify-center mr-8">
                            <div className="text-6xl opacity-30">♚</div>
                        </div>

                        {/* Match info */}
                        <div className="flex-1">
                            <div className="flex items-center gap-2 mb-2">
                                <div className="w-2 h-2 rounded-full bg-accent animate-pulse" />
                                <span className="text-xs text-accent uppercase tracking-widest">
                                    POST-MATCH ANALYSIS AVAILABLE
                                </span>
                            </div>

                            <h2 className={`font-display text-5xl mb-2 ${lastMatch.result === "win" ? "text-white" : "text-foreground-muted"
                                }`}>
                                {lastMatch.result === "win" ? "VICTORY" : lastMatch.result === "loss" ? "DEFEAT" : "DRAW"}
                            </h2>

                            <p className="text-foreground-muted mb-4">
                                VS. {lastMatch.opponentId?.username?.toUpperCase() || "OPPONENT"}
                                <span className="mx-2">•</span>
                                <span className={getRatingChangeColor(lastMatch.ratingChange)}>
                                    {lastMatch.ratingChange > 0 ? "+" : ""}{lastMatch.ratingChange} ELO
                                </span>
                                <span className="mx-2">•</span>
                                {lastMatch.gameData.moveCount} MOVES
                            </p>

                            <div className="flex gap-3">
                                <Link href={`/analysis/${lastMatch._id}`}>
                                    <Button>
                                        <Microscope className="w-4 h-4" />
                                        ANALYZE GAME
                                    </Button>
                                </Link>
                                <Button variant="secondary">
                                    <RotateCcw className="w-4 h-4" />
                                    REMATCH
                                </Button>
                            </div>
                        </div>
                    </div>
                </motion.div>
            )}

            {/* Welcome banner for new users */}
            {!lastMatch && !isLoading && (
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="bg-gradient-to-r from-accent to-accent-dark rounded-lg p-8 mb-8 text-center"
                >
                    <Crown className="w-16 h-16 text-white mx-auto mb-4" />
                    <h2 className="font-display text-4xl text-white mb-2">
                        WELCOME, {user?.username?.toUpperCase()}
                    </h2>
                    <p className="text-white/80 mb-6">
                        Ready to begin your journey to grandmaster?
                    </p>
                    <Link href="/play">
                        <Button className="bg-black hover:bg-gray-900" size="lg">
                            START YOUR FIRST GAME
                        </Button>
                    </Link>
                </motion.div>
            )}

            {/* Active Directives */}
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.1 }}
                className="mb-8"
            >
                <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-2">
                        <div className="w-1 h-6 bg-accent" />
                        <h3 className="font-display text-xl">ACTIVE DIRECTIVES</h3>
                    </div>
                    <span className="text-xs text-foreground-dim">
                        RESETS IN 04:23:12
                    </span>
                </div>

                <div className="grid grid-cols-3 gap-4">
                    {[
                        { title: "WIN 3 BLITZ GAMES", icon: Zap, progress: 1, total: 3, status: "1/3 COMPLETED" },
                        { title: "KNIGHT MATE", icon: Trophy, progress: 0, total: 1, status: "PENDING" },
                        { title: "SEASON: 50 GAMES", icon: Crown, progress: 42, total: 50, status: "42/50 PLAYED" },
                    ].map((directive, index) => (
                        <Card key={index} className="p-4">
                            <div className="flex items-center justify-between mb-3">
                                <span className="font-semibold text-white text-sm">
                                    {directive.title}
                                </span>
                                <directive.icon className="w-5 h-5 text-foreground-muted" />
                            </div>
                            <div className="h-1 bg-background-elevated rounded-full mb-2">
                                <div
                                    className="h-full bg-accent rounded-full transition-all"
                                    style={{ width: `${(directive.progress / directive.total) * 100}%` }}
                                />
                            </div>
                            <span className={`text-xs ${directive.progress === 0 ? "text-accent" : "text-foreground-dim"}`}>
                                {directive.status}
                            </span>
                        </Card>
                    ))}
                </div>
            </motion.div>

            {/* Combat Log + Allies */}
            <div className="grid grid-cols-3 gap-8">
                {/* Combat Log */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.2 }}
                    className="col-span-2"
                >
                    <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-2">
                            <div className="w-1 h-6 bg-accent" />
                            <h3 className="font-display text-xl">COMBAT LOG</h3>
                        </div>
                        <Link href="/history" className="text-accent text-sm hover:underline flex items-center gap-1">
                            VIEW FULL HISTORY
                            <ChevronRight className="w-4 h-4" />
                        </Link>
                    </div>

                    <Card className="overflow-hidden">
                        <table className="w-full">
                            <thead>
                                <tr className="border-b border-border text-left text-xs text-foreground-dim uppercase tracking-wider">
                                    <th className="p-4">RES</th>
                                    <th className="p-4">OPPONENT</th>
                                    <th className="p-4">MODE</th>
                                    <th className="p-4">DATE</th>
                                    <th className="p-4">RATING</th>
                                    <th className="p-4"></th>
                                </tr>
                            </thead>
                            <tbody>
                                {recentMatches.map((match, index) => (
                                    <tr key={match._id} className="combat-log-row">
                                        <td className="p-4">
                                            <span className={`px-2 py-1 rounded text-xs font-bold ${match.result === "win"
                                                    ? "bg-success/20 text-success"
                                                    : match.result === "loss"
                                                        ? "bg-accent/20 text-accent"
                                                        : "bg-gray-500/20 text-gray-400"
                                                }`}>
                                                {match.result === "win" ? "W" : match.result === "loss" ? "L" : "D"}
                                            </span>
                                        </td>
                                        <td className="p-4">
                                            <div className="flex items-center gap-2">
                                                <Avatar className="w-6 h-6">
                                                    <AvatarFallback className="text-xs">
                                                        {match.opponentId?.username?.slice(0, 2).toUpperCase() || "??"}
                                                    </AvatarFallback>
                                                </Avatar>
                                                <span className="text-white">
                                                    {match.opponentId?.username || "Unknown"}
                                                </span>
                                            </div>
                                        </td>
                                        <td className="p-4">
                                            <span className="flex items-center gap-1 text-foreground-muted text-sm">
                                                {match.timeControl <= 180 ? (
                                                    <><Zap className="w-3 h-3" /> BULLET</>
                                                ) : match.timeControl <= 600 ? (
                                                    <><Clock className="w-3 h-3" /> BLITZ</>
                                                ) : (
                                                    <><Clock className="w-3 h-3" /> RAPID</>
                                                )}
                                            </span>
                                        </td>
                                        <td className="p-4 text-foreground-dim text-sm">
                                            {formatTimeAgo(match.createdAt)}
                                        </td>
                                        <td className="p-4">
                                            <span className="text-white font-mono">
                                                {match.ratingAfter}
                                            </span>
                                            <span className={`ml-2 text-sm ${getRatingChangeColor(match.ratingChange)}`}>
                                                ({match.ratingChange > 0 ? "+" : ""}{match.ratingChange})
                                            </span>
                                        </td>
                                        <td className="p-4">
                                            <Link href={`/analysis/${match._id}`}>
                                                <button className="p-2 hover:bg-white/5 rounded transition-colors">
                                                    <Eye className="w-4 h-4 text-foreground-dim" />
                                                </button>
                                            </Link>
                                        </td>
                                    </tr>
                                ))}
                                {recentMatches.length === 0 && !isLoading && (
                                    <tr>
                                        <td colSpan={6} className="p-8 text-center text-foreground-dim">
                                            No matches yet. Start your first game!
                                        </td>
                                    </tr>
                                )}
                            </tbody>
                        </table>
                    </Card>
                </motion.div>

                {/* Allies Online */}
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ delay: 0.3 }}
                >
                    <div className="flex items-center justify-between mb-4">
                        <div className="flex items-center gap-2">
                            <div className="w-1 h-6 bg-accent" />
                            <h3 className="font-display text-xl">ALLIES ONLINE</h3>
                        </div>
                        <div className="flex items-center gap-1 text-xs text-success">
                            <div className="w-2 h-2 rounded-full bg-success" />
                            03 ACTIVE
                        </div>
                    </div>

                    <Card className="p-4">
                        {[
                            { name: "Queen_Gambit", status: "LOBBY", online: true },
                            { name: "Kasparov_Jr", status: "IN MATCH", online: true },
                            { name: "Bishop_Takes", status: "PUZZLE RUSH", online: true },
                        ].map((ally, index) => (
                            <div key={index} className="flex items-center gap-3 py-3 border-b border-border last:border-0">
                                <div className="relative">
                                    <Avatar className="w-10 h-10">
                                        <AvatarFallback>{ally.name.slice(0, 2)}</AvatarFallback>
                                    </Avatar>
                                    <div className="absolute bottom-0 right-0 w-3 h-3 bg-success rounded-full border-2 border-background-card" />
                                </div>
                                <div className="flex-1">
                                    <div className="font-medium text-white text-sm">{ally.name}</div>
                                    <div className={`text-xs ${ally.status === "IN MATCH" ? "text-accent" : "text-foreground-dim"}`}>
                                        {ally.status}
                                    </div>
                                </div>
                            </div>
                        ))}

                        <button className="flex items-center gap-2 text-foreground-muted hover:text-white mt-4 text-sm w-full justify-center py-2 border border-dashed border-border rounded hover:border-accent transition-colors">
                            <Users className="w-4 h-4" />
                            ADD RIVAL
                        </button>
                    </Card>
                </motion.div>
            </div>
        </div>
    );
}
